.background {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -100;
  overflow-y: auto;
  /* ff 3.6+ */
  background: -moz-linear-gradient(
    147deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(71, 243, 254, 1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-linear-gradient(
    147deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(71, 243, 254, 1) 100%
  );

  /* opera 11.10+ */
  background: -o-linear-gradient(
    147deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(71, 243, 254, 1) 100%
  );

  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#47F3FE', endColorstr='#FFFFFF', GradientType=1 );

  /* ie 10+ */
  background: -ms-linear-gradient(
    147deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(71, 243, 254, 1) 100%
  );

  /* global 94%+ browsers support */
  background: #051728;
}
