body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  min-width: 30%;
  align-items: center;
  padding: 4px;
  border: 2px solid rgb(84, 84, 141);
  border-radius: 4px;
}
.button-container--image-size {
  width: 36px;
  height: 36px;
}
.button-container--text-color {
  color: black;
}
.button-container:hover {
  cursor: pointer;
}
.modal-background {
  position: fixed;
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  width: 100%;
  height: 100%;
  z-index: 0;
}

.modal-container {
  z-index: 1;
  position: absolute;
  width: 40%;
  height: 80%;
  top: 10%;
  left: 30%;
  display: grid;
  padding-top: 2%;
  grid-template-rows: 20% 80%;
  justify-content: center;
  border-radius: 4px;
  /* ff 3.6+ */
  /* safari 5.1+,chrome 10+ */
  /* opera 11.10+ */
  /* ie 10+ */
  /* global 92%+ browsers support */
  background: radial-gradient(circle at 5% 15%, rgb(201, 191, 201) 0%, rgb(0, 131, 148) 100%);
}
.modal-container--text-size {
  color: white;
  font-size: 32px;
  text-align: center;
}
.modal-container__buttons {
  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px;
}
.header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 3.5rem;
  width: auto;
  height: 20%;
}

.logo-container {
  width: auto;
  height: auto;
  display: flex;
  flex-direction: row;
}
.logo-container__logo {
  width: 6rem;
  height: 6rem;
}
.logo-container__name {
  width: 12rem;
  height: 6rem;
  font-size: 3rem;
  line-height: 1;
  color: white;
  word-wrap: break-word;
}

.wallet-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 300px;
  height: 80px;
  color: #f6d1af;
  font-size: 1.5rem;
  border-radius: 4px;
  /* ff 3.6+ */
  /* safari 5.1+,chrome 10+ */
  /* opera 11.10+ */
  background: -o-linear-gradient(90deg, rgb(91, 14, 100) 0%, rgb(78, 17, 253) 100%);
  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#4E11FD", endColorstr="#5B0E64", GradientType=0 );
  /* ie 10+ */
  /* global 94%+ browsers support */
  background: linear-gradient(90deg, rgb(91, 14, 100) 0%, rgb(78, 17, 253) 100%);
}
.wallet-container__image {
  width: 36px;
  height: 36px;
}
.wallet-container:hover {
  cursor: pointer;
}

@media screen and (max-width: 720px) {
  .header-container {
    justify-content: space-evenly;
    align-items: center;
  }
  .logo-container__name {
    display: none;
  }
}
@media screen and (max-width: 600px) {
  .logo-container__logo {
    display: none;
  }
  .logo-container__name {
    display: none;
  }
  .wallet-container {
    margin: auto;
  }
  .header-container {
    justify-content: center;
    align-items: center;
  }
}
.info-container {
  display: grid;
  grid-template-columns: 40% 8% 40%;
  justify-content: center;
  color: white;
  margin: 2rem 0;
}
.info-container__image {
  width: 100%;
  height: 100%;
  background-color: lightgray;
  grid-column-start: 1;
  grid-column-end: 2;
}
.info-container__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  grid-column-start: 3;
}
.info-container__content__greeting {
  font-size: 5rem;
}
.info-container__content__statement {
  font-size: 1rem;
}
.info-container__content__contact {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.info-container__content__contact__wallet {
  display: grid;
  grid-template-columns: auto 80%;
  justify-content: start;
  align-items: center;
  margin-bottom: 2rem;
  justify-self: center;
  font-size: 1rem;
  font-weight: bold;
  word-wrap: break-word;
}
.info-container__content__contact__wallet__image {
  width: 100%;
  height: 100%;
}
.info-container__content__contact__social {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media screen and (max-width: 900px) {
  .info-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    margin: 2rem;
  }
  .info-container__content__greeting {
    font-size: 5rem;
    text-align: center;
  }
  .info-container__content__contact {
    width: 100%;
  }
  .info-container__content__contact__wallet {
    grid-template-columns: auto auto;
    justify-content: center;
  }
  .info-container__content__contact__wallet__image {
    justify-self: center;
  }
}
.invalid-network-container {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.invalid-network-container__title {
  display: flex;
  align-items: center;
  background-color: #f04444;
  font-size: 2rem;
  border-radius: 4px;
  margin-bottom: 1rem;
  padding: 1rem;
}
.invalid-network-container__title__image {
  width: min(5vw, 5vh);
  margin-right: 1rem;
}
.invalid-network-container__body {
  font-size: 1rem;
}
.notification-container {
  position: absolute;
  display: flex;
  top: 15.5%;
  right: -50%;
  min-width: 200px;
  min-height: 24px;
  padding: 12px;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  color: white;
  -webkit-animation-name: slide-in-slide-out;
          animation-name: slide-in-slide-out;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
}
.notification-container--message {
  font-size: 1.5rem;
  line-height: 0.25;
}

body {
  overflow-x: hidden;
}

@-webkit-keyframes slide-in-slide-out {
  10%, 80% {
    right: 3.5rem;
  }
  90% {
    right: -50%;
  }
}

@keyframes slide-in-slide-out {
  10%, 80% {
    right: 3.5rem;
  }
  90% {
    right: -50%;
  }
}
.media-container {
  width: 100%;
  height: 100%;
}
.media-container__image {
  width: 100%;
  height: 100%;
}
.project-text-container {
  display: flex;
  flex-direction: column;
}
.project-text-container__title {
  margin: 0;
  margin-bottom: 1rem;
  font-size: 4rem;
}
.project-text-container__text {
  font-size: 1rem;
}
.project-text-container__text__point {
  margin: 0;
}

@media screen and (max-width: 900px) {
  .project-text-container__title {
    text-align: center;
  }
}
.loading-container {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  -webkit-animation: FadeInFadeOutTranslucent 1.5s infinite;
          animation: FadeInFadeOutTranslucent 1.5s infinite;
}
.loading-container--text-size {
  font-size: 48px;
}

@-webkit-keyframes FadeInFadeOutTranslucent {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes FadeInFadeOutTranslucent {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}
.web3-message-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.web3-message-container__loading {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
}
.web3-message-container__title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  text-align: center;
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
  margin-bottom: 0.4rem;
  /* ff 3.6+ */
  /* safari 5.1+,chrome 10+ */
  /* opera 11.10+ */
  background-image: -o-linear-gradient(90deg, rgb(194, 253, 188) 25%, rgb(172, 225, 18) 100%);
  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#ACE112", endColorstr="#C2FDBC", GradientType=0 );
  /* ie 10+ */
  /* global 94%+ browsers support */
  background-image: linear-gradient(90deg, rgb(194, 253, 188) 25%, rgb(172, 225, 18) 100%);
}
.web3-message-container__title__image {
  display: flex;
  flex-direction: row;
  width: 10%;
  margin-right: 0.5rem;
}
.web3-message-container__upvoted {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.web3-message-container__interactive {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.web3-message-container__interactive__write {
  font-size: 1rem;
  margin-right: 1rem;
}
.web3-message-container__interactive__button {
  display: flex;
  flex-direction: row;
  width: 50%;
  padding: 4px;
  justify-content: space-around;
  align-items: center;
  border-radius: 4px;
  /* ff 3.6+ */
  /* safari 5.1+,chrome 10+ */
  /* opera 11.10+ */
  background: -o-linear-gradient(90deg, rgb(91, 14, 100) 0%, rgb(78, 17, 253) 100%);
  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#4E11FD", endColorstr="#5B0E64", GradientType=0 );
  /* ie 10+ */
  /* global 94%+ browsers support */
  background: linear-gradient(90deg, rgb(91, 14, 100) 0%, rgb(78, 17, 253) 100%);
}
.web3-message-container__interactive__button__text {
  font-size: 1.2rem;
  color: #f6d1af;
}
.web3-message-container__interactive__button__image {
  width: 48px;
}
.web3-message-container__interactive__button:hover {
  cursor: pointer;
  box-shadow: inset 0 0 0 2px black;
}

@media screen and (max-width: 900px) {
  .web3-message-container__interactive__button__text {
    display: none;
  }
}
.project-content-container {
  display: grid;
  grid-template-columns: 45% 10% 45%;
  padding: 3rem 3rem;
  background-color: #092e50;
  color: white;
  border-top: 4px solid black;
  border-bottom: 4px solid black;
}
.project-content-container__text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  grid-column-start: 1;
  grid-column-end: 2;
}
.project-content-container__media {
  grid-column-start: 3;
  grid-column-end: 3;
}

@media screen and (max-width: 900px) {
  .project-content-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .project-content-container > * {
    margin-bottom: 1rem;
  }
}
.project-list-container {
  min-height: 80vh;
  display: grid;
  grid-template-rows: 90% 10%;
}
.project-list-container__navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: white;
  font-size: 1.25rem;
  margin: 0 8px;
}
.project-list-container__navigation--hide {
  visibility: hidden;
}
.background {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -100;
  overflow-y: auto;
  /* ff 3.6+ */
  /* safari 5.1+,chrome 10+ */
  /* opera 11.10+ */
  background: -o-linear-gradient(147deg, rgb(255, 255, 255) 0%, rgb(71, 243, 254) 100%);
  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#47F3FE", endColorstr="#FFFFFF", GradientType=1 );
  /* ie 10+ */
  background: -ms-linear-gradient(147deg, rgb(255, 255, 255) 0%, rgb(71, 243, 254) 100%);
  /* global 94%+ browsers support */
  background: #051728;
}
